* {
  padding: 0%;
  margin: 0%;
  scroll-behavior: smooth;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  background: #131313;
  color: #fff;
}

/* start of search bar */

.search {
  width: 100%;
  position: absolute;
  top: 1%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search input::placeholder {
  color: #037fff;
}

.search input {
  width: 30vw;
  padding: 10px 10px;
  outline: none;
  text-align: center;
  border: .15rem solid #037fff;
  color: #037fff;
  font-size: 1.2rem;
  border-radius: 20px;
}

.search input:focus {
  border: 1px solid #037fff;
}

/* End of search bar */

/* Start of button page styles */

.button {
  display: flex;
  align-items: center;
  justify-content: center;     
}

.button button {
  display: inline-block;
  border: none;
  outline: none;
  background-color: #037fff;
  color: #fff;
  padding: 10px 10px;
  font-size: 1.1rem;
  margin: 4.5rem .5rem .4rem .4rem;
}

.button button:hover {
  opacity: .8;
}

.button button:focus-within {
  background-color: #8A0707;
  transition: all .5s ease-out;
}

/* End of button page styles */

/* Start of cart page styles */

.container {
  width: 25%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.card {
  width: 100%;
  min-height: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 8px 8px 4px rgba(0, 0, 0, 0.5);
  background-color: #fff;
  overflow: hidden;
  border-radius: 20px;
  margin: 1rem;
  color: black;
  padding: 5px 5px;
}

.card img {
  display: flex;
  width: 100%;
  height: 40vh;
  object-fit: cover;
  padding: .5rem .5rem;
  border-radius: 1rem;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

.card img:hover {
  transform: scale(2);
}

.card h3 {
  font-size: 1.2rem;
  color: blueviolet;
  margin: .8rem;
}

.card h6 {
  font-size: 1.2rem;
  margin: 0.5rem;
  text-transform: capitalize;
  font-weight: 200;
  text-align: center;
}

/* End of cart page styles */